import { v4 } from "uuid";
import types from "./actionTypes";

const initState = {
  notifications: [],
  foldersLoading: false,
  creatingFolder: false,
  patchingTodo: false,
};

export const uiReducer = (state = initState, { type, data }) => {
  switch (type) {
    case types.SET_NOTIFICATION:
      return {
        ...state,
        notifications: [
          ...state.notifications,
          {
            header: data.header,
            body: data.body,
            id: data.id,
          },
        ],
      };
    case types.REMOVE_NOTIFICATION:
      return {
        ...state,
        notifications: [
          ...state.notifications.filter(
            notification => notification.id !== data.id
          ),
        ],
      };
    case types.SET_FOLDERS_LOADING:
      return { ...state, foldersLoading: true };
    case types.RESET_FOLDERS_LOADING:
      return { ...state, foldersLoading: false };
    case types.SET_PATCHING_TODO:
      return { ...state, patchingTodo: true };
    case types.RESET_PATCHING_TODO:
      return { ...state, patchingTodo: false };
    default:
      return state;
  }
};

export const setNotification = ({ header, body }) => {
  return {
    type: types.SET_NOTIFICATION,
    data: { header, body, id: v4() },
  };
};

export const removeNotification = id => {
  return {
    type: types.REMOVE_NOTIFICATION,
    data: { id },
  };
};

export const setFoldersLoading = () => ({ type: types.SET_FOLDERS_LOADING });

export const resetFoldersLoading = () => ({
  type: types.RESET_FOLDERS_LOADING,
});

export const setEditingTodo = () => ({
  type: types.SET_PATCHING_TODO,
});

export const resetEditingTodo = () => ({
  type: types.RESET_PATCHING_TODO,
});
