import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { patchTodoInfo } from "../../reducers/collectionReducer";
import "./TodoItemFull.scss";
import PropTypes from "prop-types";
import Backdrop from "../Shared/UI/Backdrop";
import LoadingSkeleton from "../Shared/UI/LoadingSkeleton";

const TodoItemFull = ({ todo }) => {
  const { title, description, checked, id } = todo;
  const [inEditMode, setInEditMode] = useState(false);
  const [currNewTitle, setCurrNewTitle] = useState(title);
  const [currNewDescription, setCurrNewDescription] = useState(description);

  const history = useHistory();
  const dispatch = useDispatch();
  const userData = useSelector(({ user }) => user.data);
  const isTodoLoading = useSelector(({ ui }) => ui.patchingTodo);

  useEffect(() => {
    if (isTodoLoading) console.log("Loading...");
    else console.log("Not loading");
  }, [isTodoLoading]);

  const closeViewHandler = e => {
    console.log(e.target);
    if (e.target.parentNode.id === "backdrop" || e.target.id === "close__btn")
      history.push(`/${userData.username}/home`);
  };

  const toggleEditModeHandler = () => {
    if (inEditMode) {
      setCurrNewTitle(title);
      setCurrNewDescription(description);
    }
    setInEditMode(prev => !prev);
  };

  const saveChangesHandler = async () => {
    if (!currNewDescription.trim().length || !currNewTitle.trim().length)
      return console.log("fatal error");
    await dispatch(
      patchTodoInfo(id, {
        title: currNewTitle,
        description: currNewDescription,
      })
    );
  };

  const changeEditHandler = e => {
    const { name, value } = e.target;
    switch (name) {
      case "new-title":
        return setCurrNewTitle(value);
      case "new-description":
        return setCurrNewDescription(value);
      default:
        return;
    }
  };

  return (
    <Backdrop onClick={closeViewHandler}>
      <div className="todo__container">
        {inEditMode ? (
          isTodoLoading ? (
            <>
              <div className="loading-title"></div>
              <div className="loading-description-container">
                <div className="loading-description">
                  <LoadingSkeleton />
                </div>
                <div className="loading-description">
                  <LoadingSkeleton />
                </div>
                <div className="loading-description">
                  <LoadingSkeleton />
                </div>
                <div className="loading-description">
                  <LoadingSkeleton />
                </div>
                <div className="loading-description">
                  <LoadingSkeleton />
                </div>
              </div>
            </>
          ) : (
            <>
              <input
                name="new-title"
                className="edit__title"
                value={currNewTitle}
                onChange={changeEditHandler}
              />
              <textarea
                name="new-description"
                className="edit__description"
                onChange={changeEditHandler}
                defaultValue={currNewDescription}
              ></textarea>
            </>
          )
        ) : (
          <>
            <h2 className="todo__title">{title}</h2>
            <p className="todo__description">
              {description
                ? description
                : "There is no description for this todo..."}
            </p>
          </>
        )}
        <p className="todo__status">
          Status:{" "}
          <span className={`${checked ? "completed" : "not_completed"}`}>
            {checked ? "completed  " : "not completed "}
          </span>
        </p>
        <div className="control__btn__container">
          {inEditMode ? (
            <>
              <button onClick={toggleEditModeHandler}>Cancel</button>
              <button onClick={saveChangesHandler}>Save</button>
            </>
          ) : (
            <>
              <button id="close__btn" onClick={closeViewHandler}>
                Close
              </button>
              <button onClick={toggleEditModeHandler}>Edit</button>
            </>
          )}
        </div>
      </div>
    </Backdrop>
  );
};

TodoItemFull.propTypes = {
  todo: PropTypes.shape({
    title: PropTypes.string.isRequired,
    description: PropTypes.string,
    checked: PropTypes.bool.isRequired,
    id: PropTypes.string.isRequired,
  }).isRequired,
};

export default TodoItemFull;
